* {
  margin: 0;
  padding: 0;
}
.greenText {
    color: #057453
}

* {
    font-family: 'Roboto', sans-serif;
}


#header {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    border: solid 1px red;
    height: 5%;
    text-align: center;
}

#offerCode{
    border: solid 1px blue;
    width: 80%;
}

#storeLocator{
    background: #057453;
    border: solid 1px blue;
    width: 20%;
}


#navbar {
    border: solid 1px red;
    width: 100%;
    padding: 25px 0;
    background: #0E141D;
    position: relative;
    color: white;
}

#navbar ul {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-content: center;
            align-content: center;
    list-style: none;
    padding-left: 15%;
} 

#logo {
    position: absolute;
    top : 0%;
    left : 2%;
    width: 10% ;
    max-width: 150px;
    min-width: 100px;
}
#main {
    border: solid 1px red;
}


#container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    /* border: solid 1px red; */
    height: 90vh;
    width: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    color: white;

}

#icedCoffee {
    /* border : solid 1px blue; */
    width: 30%;
}



#heading {
    /* border: solid 1px red; */
    position: relative;
    right: 30%;
    width: 30%;
}
#span {
    border: solid 1px red;
    text-align: center;
    padding: 1%;
}


#tilecontainer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: row;
            flex-direction: row;
    border: solid 1px red;
    height: 80vh;

}
#tile {
    border: solid 1px blue;
    width: 30%;
    margin: 1%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    color: black;

}

#roundImages {
    border-radius: 50%;
    height: 300px;
    width: 65%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}


#navfooter {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    border: solid 1px red;
}
#linktile {
    border: solid 1px red;
    width: 25%;
    height: 25%;
}
#footer {
    border: solid 1px red;
}
